<script>
import TitleBar from '@/components/common/title-bar'
import Echarts from 'echarts'


export default {
    name:'query-page',
    created(){
      
    },
    mounted(){
        this.drawLine();
        this.drawLine2();


    },
    methods:{
        drawLine(){
            // domecharts
            let myChart = Echarts.init(document.getElementById('myChart'))
            // 
            myChart.setOption({
                title: { text: '',
                    textStyle:{
                        color:'#4A4A4A ',
                        fontSize:14
                    }
                },
                legend: {  //
                    //right:100,  //
                    orient : 'horizontal',  //    vertical
                    width:40,      //,
                    x : 'left',   //
                    y: 'center',   //
                    itemWidth:16,  //
                    itemHeight:16, //
                    data:['> 1 ','> 2 ','> 3 ','> 4 '],
                    textStyle:{    //
                        color:'#333',  //
                        fontSize:14    //
                    }
                },
                series: [{
                    name: '',
                    type: 'pie',
                    center:['65%','50%'],
                    radius:['30%','80'],
                    itemStyle :{
                        normal : { //normal emphasis 
                                label : {  //
                                    show : false  //
                                },
                                labelLine : {     //
                                    show : false  //
                                }
                            }
                    },
                    emphasis : {   //normal emphasis 
                                label : {  //
                                    show : true,
                                    position : 'center',
                                    textStyle : {
                                        fontSize : '10',
                                        fontWeight : 'bold'
                                    }
                                }
                            },
                    data:[
                            {value:80, name:'> 1 '},
                            {value:10, name:'> 2 '},
                            {value:30, name:'> 3 '},
                            {value:20, name:'> 4 '},
                            
                        ]


                }]
            });
        },
         drawLine2(){
            let option = {
                title : {
                    text: '',
                },
             
                legend: {
                    data:['','',''],
                    orient : 'horizontal',  //    vertical
                    x : 'right',   //
                    y: 'top',   //
                    itemWidth:16,  //
                    itemHeight:16, //

                },
                calculable : false,
                xAxis : [
                    {
                        type : 'category',
                        boundaryGap : false,
                        data : ['','','','','','','']
                    }
                ],
                yAxis : [
                    {
                        type : 'value'
                    }
                ],
                series : [
                    {
                        name:'',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[10, 12, 21, 54, 260, 830, 710]
                    },
                    {
                        name:'',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[30, 182, 434, 791, 390, 30, 10]
                    },
                    {
                        name:'',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[1320, 1132, 601, 234, 120, 90, 20]
                    }
                ]
            };


            let myChart = Echarts.init(document.getElementById('myChart2'));
            myChart.setOption(option);

                                



         }
            
    },
    components:{
	'v-titlebar':TitleBar,
    },
}
</script>


<template>
    <div class = "query3">
            <div>
                <v-titlebar></v-titlebar>
            </div>
            <div class="main">
                <div class = "temp-value">
                    <div class = "temp-value-top">
                        <div class = "temp-value-top-left">
                            <img src = "../assets/queryPage/temp-select.png"/>
                            <span style = "font-size:36px;color:#4AB6A7 ">26.8</span>
                        </div>
                        <div class = "temp-value-top-right">
                            <div style = "font-size:12px;color:#4A4A4A "></div>
                            <div style = "font-size:18px;color:#4AB6A7">+12</div>
                        </div>
                    </div>
                    <div  class = "temp-value-bottom">
                        <img src = "../assets/queryPage/location.png"/>
                        <span style = "font-size:14px;color:#9B9B9B ">12A</span>
                    </div>
                </div>


                <div class = "temp-range">
                    <div>
                        <div id="myChart" style = "width:6.64rem;height:3.9rem"></div>
                    </div>
                </div>

                <div class = "temp-diff">
                    <div>
                        <div id="myChart2" style = "width:6.64rem;height:3.9rem"></div>
                    </div>
                </div>
            </div>
    </div>
</template>

<style lang="less" scoped>
    .main {
        padding:1.12rem 0.32rem  0.43rem;
        margin-bottom: 1rem;
        height:e("calc(100vh - 4rem)");
        .temp-value, .temp-range,.temp-diff {
            border:1px solid #C7DDDA;
            border-radius: 3px;
            background-color: #FFF;
        }
        >div + div {
            margin-top:0.32rem;
        }
        .temp-value {
            padding:0.32rem 0.32rem 0;
            .temp-value-top,.temp-value-bottom {
                display: flex;
                padding:0.32rem;
            }

            .temp-value-top {
                border-bottom:1px dashed #C7DDDA;
                padding-bottom: 0.32rem;
                > div {
                    flex:1;
                    display: flex;
                    justify-content: flex-start;
                }
                .temp-value-top-left {
                    align-items: center;
                    justify-content: flex-start;
                    img {
                        width:0.64rem;
                        height:0.64rem;
                        margin-right:0.32rem;
                    }
                    
                }
                .temp-value-top-right {
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-end;
                }
            }
            .temp-value-bottom {
                img{
                    width:0.5rem;
                    height:0.5rem;
                }
            }

         }

         .temp-range,.temp-diff {
             padding:.32rem;

         }

         

    
    
    
    
    
    
    
    
    }

</style>



